import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';

import { Card, Row, message, Col, Checkbox } from 'antd';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import { getSemesters, deleteSemester } from 'services/semesterService';
import './styles.scss';

class ListSemesterPage extends Component {
  state = { reload: 0 };
  handleDelete = async id => {
    await deleteSemester(id);
    message.success('Xa thnh cng');
    this.setState({ reload: this.state.reload + 1 });
  };

  render() {
    const { reload } = this.state;
    const columns = [
      {
        title: 'M hc k',
        dataIndex: 'id',
        key: 'id',
      },
      {
        title: 'Tn hc k',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Hc k chnh',
        dataIndex: 'isMain',
        key: 'isMain',
        render: (text, record) => <Checkbox checked={record.isMain} />,
      },
      {
        title: 'Thao tc',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`#Semester/form/edit/${record.id}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="Khu vc" className="semester">
        <Row>
          <Col span={24}>
            <CartHeadAction>
              <BtnAddNew href="#Semester/form/new" />
            </CartHeadAction>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData reload={reload} service={getSemesters} columns={columns} rowKey="id" />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListSemesterPage />
  </Page>
);



